:root {
  --rob: "Roboto", sans-serif;
  --great: "Great Vibes", cursive;
  --bg_gradient: linear-gradient(45deg, #F5FDFF, #FEEFEC);
  --bbpc_brand_color: var(--bbpc_brand_color_opt,#0866ff);
  --h_title: #1d2746;
  --p_color: #6b707f;
  --bs-white: #fff;
  --black_25: #f9f9f9;
  --black_50: #f2f2f2;
  --black_75: #eeeeee;
  --black_90: #e0e6f0;
  --black_100: #d6d9dc;
  --black_150: #c8ccd0;
  --black_200: #bbc0c4;
  --black_300: #9fa6ad;
  --black_350: #9199a1;
  --black_400: #848d95;
  --black_500: #6a737c;
  --black_600: #535a60;
  --black_700: #3c4146;
  --black_750: #2f3337;
  --black_800: #242729;
  --black_900: #0c0d0e;
  --bbpc_color: #078669;
  --baseColor2: #4a6ee0;
  --bs_white: #fff;
}

/*font Variables*/
/**=== Functions ===**/
.col-md-offset-right-1,
.col-lg-offset-right-1 {
  margin-right: 8.33333333%;
}

.communities-boxes {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -30px;
  gap: 30px;
}
@media (max-width: 1025px) {
  .communities-boxes {
    margin: 0 -10px;
  }
}
@media (max-width: 992px) {
  .communities-boxes {
    flex-wrap: wrap;
    justify-content: center;
    width: calc(100% + 30px);
  }
}
@media (max-width: 420px) {
  .communities-boxes {
    width: calc(100% + 15px);
  }
}
.communities-boxes .com-box {
  text-align: center;
  margin-bottom: 57px;
  width: 20%;
  flex: 0 0 200px;
}
@media (max-width: 1200px) {
  .communities-boxes .com-box {
    flex: 0 0 165px;
  }
}
@media (max-width: 991px) {
  .communities-boxes .com-box {
    flex: 0 0 180px;
  }
}
@media (max-width: 768px) {
  .communities-boxes .com-box {
    flex: 0 0 185px;
  }
}
@media (max-width: 426px) {
  .communities-boxes .com-box {
    flex: 0 0 170px;
    margin-bottom: 30px;
  }
}
.communities-boxes .com-box .icon-container {
  border-radius: 6px;
  background: var(--bs-white);
  box-shadow: 0 40px 70px 0 rgba(2, 47, 57, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 100px;
  margin: 0 auto 20px;
}
.communities-boxes .com-box .icon-container img {
  width: 40px;
  height: 40px;
  object-fit: cover;
}
.communities-boxes .com-box .com-box-content .title {
  font-size: 20px;
  line-height: 26px;
  font-weight: 500;
  color: var(--black_800);
  margin-bottom: 5px;
  transition: all 0.3s ease-in-out;
  letter-spacing: normal;
  margin-top: 10px;
}
.communities-boxes .com-box .com-box-content .title a {
  color: var(--black_800);
}
.communities-boxes .com-box .com-box-content .title a:hover {
  color: var(--bbpc_brand_color);
  text-decoration: none;
}
.communities-boxes .com-box .com-box-content .title:hover {
  color: var(--bbpc_brand_color);
}
.communities-boxes .com-box .com-box-content .total-post {
  color: var(--p_color);
  font-size: 14px;
  margin-top: 0;
}

.more-communities {
  padding: 22px 0;
  border-top: 1px solid #e8ecee;
  border-bottom: 1px solid #e8ecee;
}
.more-communities .collapse-wrap {
  margin-top: 80px;
  display: none;
}
.more-communities .collapse-btn-wrap {
  text-align: center;
  display: table;
  color: var(--black_600);
  font-weight: 500;
  margin: auto;
}
.more-communities .collapse-btn-wrap:hover {
  text-decoration: none;
  color: var(--black_800);
}
.more-communities .collapse-btn-wrap svg {
  position: relative;
  vertical-align: middle;
  display: inline;
}
.more-communities .collapse-btn-wrap .icon_plus path, .more-communities .collapse-btn-wrap .icon_plus line, .more-communities .collapse-btn-wrap .icon_minus path, .more-communities .collapse-btn-wrap .icon_minus line {
  stroke: var(--bbpc_brand_color);
}
.more-communities .collapse-btn-wrap .icon_minus {
  display: none;
}
.more-communities .collapse-btn-wrap.active .icon_plus {
  display: none;
}
.more-communities .collapse-btn-wrap.active .icon_minus {
  display: initial;
}

body.body_dark .communities-boxes .com-box .com-box-content .title a {
  color: #fff;
}
body.body_dark .communities-boxes .com-box .com-box-content .title a:hover {
  color: var(--bbpc_brand_color);
}
body.body_dark .communities-boxes .com-box .com-box-content .total-post {
  color: #8b8b8b;
}
body.body_dark .more-communities {
  border-color: #272727;
}
body.body_dark .single-forum-post-widget .post-info div {
  color: #848d95;
}
body.body_dark .single-forum-post-widget .post-info .post-category a {
  color: #848d95;
}
body.body_dark .single-forum-post-widget .post-info .post-category a:hover {
  color: var(--bbpc_brand_color);
}
body.body_dark .community-area .nav-tabs .nav-item button.active {
  color: #fff;
}

/** === Widgets === **/
.widget_display_stats dl dt {
  float: left;
  clear: left;
  margin: 0 0 10px;
  font-weight: 500;
}

.widget_display_stats dl dd {
  float: right;
  margin: 0;
}

.widget_display_replies li:before {
  font-family: eleganticons;
  content: "w";
  font-size: 15px !important;
  margin: 0 12px 0 0 !important;
  padding: 0px !important;
  line-height: 23px;
}

.widget_display_topics li div {
  color: var(--black_400);
}
.widget_display_topics li::before {
  font-family: eleganticons;
  content: "b";
  margin: -3px 12px 0 0;
  font-size: 15px;
  color: var(--black_800);
  position: absolute;
  left: 0;
  top: 0;
}

.widget_display_topics .bbp-forum-title,
.widget_display_replies .bbp-forum-title {
  display: inline-block;
  line-height: 1.36;
  color: var(--black_900);
}

.widget_display_topics li {
  padding-left: 30px;
}

.widget_display_topics li,
.widget_display_replies li {
  position: relative;
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #e5e7e9;
  font-size: 16px;
}
.widget_display_topics li time,
.widget_display_replies li time {
  color: var(--black_400);
  display: block;
  font-size: 14px;
}
.widget_display_topics li div,
.widget_display_replies li div {
  font-size: 14px;
}

.widget_display_topics li a,
.widget_display_replies li a {
  color: var(--black_800);
}
.widget_display_topics li a .bbp-author-avatar img,
.widget_display_replies li a .bbp-author-avatar img {
  border-radius: 50px;
}

.widget_display_replies li .bbp-author-avatar {
  margin-right: 5px;
}

.widget_display_topics li a:hover,
.widget_display_replies li a:hover {
  color: var(--bbpc_brand_color);
}

.bbp-logged-in .avatar {
  float: left;
  margin: 0 15px 0 0;
  border-radius: 99%;
}

.bbp-logged-in h4 {
  font-weight: normal;
  font-size: 18px;
  clear: none;
  margin: 0 0 5px;
}

.bbp-logged-in h4 a {
  color: #333;
  font-weight: 500;
}

.bbp-logged-in a.button.logout-link:hover,
.bbp-logged-in h4 a:hover {
  color: var(--bbpc_brand_color);
}

.bbp-logged-in a.button.logout-link:hover,
.bbp-logged-in a.button.logout-link {
  color: #666;
}

.forum-post-content .content img {
  max-width: 100%;
  height: auto;
}

ul.bbp-topics-widget.newness img {
  border-radius: 50%;
  margin: 5px;
}

.bbp-submit-wrapper {
  float: left;
  margin-top: 0;
}

.forum-post-content .forum-post-btm .taxonomy.forum-post-cat {
  display: flex;
}

.bbp-topic-reply-link {
  padding: 6px 20px;
  font-weight: 400;
  border-radius: 2px;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.18s ease-in-out;
  background: #fff;
  border: 1px solid #d0d8dc;
  color: var(--black_800);
  height: 26px;
  font-size: 12px;
  line-height: 26px;
}

.comment-content .bbp-topic-reply-link:hover {
  background: #fff;
  border: 1px solid var(--black_800);
  color: var(--black_800);
}

.page-numbers {
  cursor: pointer !important;
}

.pagination-wrapper .page-numbers.next {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 2px;
}

.post-header .support-total-info li a {
  font-weight: 400;
}

.post-header .support-total-info li a {
  color: #6b707f;
}

.post-header .support-total-info li a.open-data.loading {
  color: var(--black_800);
  font-weight: 500;
}

#tagList {
  padding-left: 40px;
}

.userlist {
  padding-left: 15px;
}

.post-header .category-menu .all-users a {
  position: relative;
}

.post-header .category-menu .all-users a .count {
  background: #ebedf0;
  border-radius: 10px;
  color: #838793;
  font-size: 12px;
  position: absolute;
  margin-left: 5px;
  padding: 1px 5px 0;
  top: 8px;
  width: auto;
  height: auto;
}